export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'ಸಮಸ್ಯೆಯನ್ನು ವರದಿ ಮಾಡಿ';
            case 'edit_pratilipi_summary':
            	return 'ಸಾರಾಂಶ ((ಐಚ್ಛಿಕ)';
            case 'edit_pratilipi_title':
            	return 'ಶೀರ್ಷಿಕೆ*';
            case 'pratilipi_delete_content':
                return 'ಡಿಲೀಟ್ ಮಾಡಿ';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['ಸಮಸ್ಯೆಯನ್ನು ವರದಿ ಮಾಡಿ'];
            case 'edit_pratilipi_summary':
            	return ['ಸಾರಾಂಶ (ಐಚ್ಛಿಕ)'];
            case 'edit_pratilipi_title':
            	return ['ಇಲ್ಲಿ ಶೀರ್ಷಿಕೆ ನಮೂದಿಸಿ', 'ಆಂಗ್ಲ ಭಾಷೆಯಲ್ಲಿ ಶೀರ್ಷಿಕೆಯನ್ನು ಮರು-ನಮೂದಿಸಿ'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'ನೀವು ಈ ಬರಹವನ್ನು ಡಿಲೀಟ್ ಮಾಡಲು ಬಯಸುವಿರಾ?';
        }
    }
}



