import Vue from 'vue'

Vue.filter('round', function(value, decimals) {
    if (!value) {
        value = 0;
    }

    if (!decimals) {
        decimals = 0;
    }

    value = Math.round(value * Math.pow(10, decimals)) / Math.pow(10, decimals);
    return value;
});


Vue.filter('showInMinutesOrHours', function(value) {
    if (!value) {
        value = 0;
    }

    let finalMinutes = Math.round(value / 60);
    let finalHours = Math.round(finalMinutes / 60);

    if (finalMinutes === 0) finalMinutes = 1;

    let readingTimeInNativeLanguage;
    if (finalHours > 1) {
        readingTimeInNativeLanguage = finalHours + ' ' + "ಗಂಟೆಗಳು";
    } else if (finalHours === 1) {
        readingTimeInNativeLanguage = finalHours + ' ' + "ಗಂಟೆ";
    } else if (finalMinutes > 1) {
        readingTimeInNativeLanguage = finalMinutes + ' ' + "ನಿಮಿಷಗಳು";
    } else {
        readingTimeInNativeLanguage = finalMinutes + ' ' + "ನಿಮಿಷ";
    }

    return readingTimeInNativeLanguage;
});

Vue.filter('getPratilipiTypeInNativeLanguage', function(value) {
    switch (value) {
        case 'STORY':
            return "ಕಥೆ";
        case 'POEM':
            return "ಕವಿತೆ";
        case 'ARTICLE':
            return "ಲೇಖನ";
        case 'BOOK':
            return "ಪುಸ್ತಕ";
        case 'MAGAZINE':
            return "ನಿಯತಕಾಲಿಕೆ";
    }
});

Vue.filter('convertDate', function(value) {
    const d = new Date(value);

    function day(d) { return (d < 10) ? '0' + d : d; }

    function month(m) {
        var months = [
            "ಜನವರಿ", 
            "ಫೆಬ್ರವರಿ", 
            "ಮಾರ್ಚ್",
            "ಏಪ್ರಿಲ್", 
            "ಮೇ", 
            "ಜೂನ್",
            "ಜುಲೈ", 
            "ಆಗಸ್ಟ್", 
            "ಸೆಪ್ಟೆಂಬರ್",
            "ಅಕ್ಟೋಬರ್", 
            "ನವೆಂಬರ್", 
            "ಡಿಸೆಂಬರ್"
        ];
        return months[m];
    }
    return [day(d.getDate()), month(d.getMonth()), d.getFullYear()].join(' ');
});



